'use strict';

define(['text!./view.html', 'knockout', 'app/config', 'lib/auth', 'lib/url', 'lib/knockout-bindings-modal', 'lib/knockout-bindings-iframe'], function (template, ko, config, auth, url) {

	"use strict";

	var _browserUrl = config && config.fileBrowser && config.fileBrowser.browserUrl || "/browser/index.htm";

	function ViewModel() {

		var self = this;

		self.src = _browserUrl;
		self.onload = function () {

			(this.contentWindow || this).Run(url.resolveApi(""), auth.token);
		};
	}

	return {
		viewModel: ViewModel,
		template: template
	};
});